.pageTitle {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}

.innerCard {
  background-color: #212020;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  padding: 30px;
  color: #000;
  margin-bottom: 20px; /* Adds space between sections */
}

.cardContainer {
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  width: 100%;
  margin: 0 auto;
}

.card-body {
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  max-width: 1200px;
  margin: auto;
  padding: 30px;
}
/* Inside product.module.css */


.imageUploadBox {
  width: 100%;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #f9f9f9;
  text-align: center;
}

.imageUploadBox img {
  max-width: 100%;
  border-radius: 8px;
  margin-top: 10px;
}
